@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap);
.modal {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 99;
  background-color: rgba(0, 0, 0, 0.6);
}

.modal button {
  outline: none;
  cursor: pointer;
  border: 0;
}

.modal > section {
  width: 90%;
  max-width: 450px;
  margin: 0 auto;
  border-radius: 0.3rem;
  background-color: #fff;
  -webkit-animation: modal-show 0.3s;
          animation: modal-show 0.3s;
  overflow: hidden;
}

.modal > section > header {
  position: relative;
  padding: 16px 64px 16px 16px;
  background-color: #f1f1f1;
}

.modal > section > header button {
  position: absolute;
  top: 15px;
  right: 15px;
  width: 30px;
  background-color: transparent;
}

.modal.openModal {
  display: flex;
  align-items: center;
  -webkit-animation: modal-bg-show.3s;
          animation: modal-bg-show.3s;
}

@-webkit-keyframes modal-show {
  from {
    opacity: 0;
    margin-top: -50px;
  }
  to {
    opacity: 1;
    margin-top: 0;
  }
}

@keyframes modal-show {
  from {
    opacity: 0;
    margin-top: -50px;
  }
  to {
    opacity: 1;
    margin-top: 0;
  }
}

@-webkit-keyframes modal-bg-show {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes modal-bg-show {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

html,
body {
  font-family: "Poppins", "Noto Sans KR" sans-serif;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
}

body {
  width: 100%;
  height: 100%;
  margin: auto;
  user-select: none;
  -webkit-user-select: none;
  -webkit-user-drag: none;
  -ms-user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
}

#root {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

/* Common Layout */
header {
  width: 100%;
  height: 50px;
  position: fixed;
  top: 0;
  left: 0;
}

.container {
  width: 100%;
  height: 100%;
  min-height: 100%;
  position: relative;
  padding-top: 50px;
  z-index: -10;
  margin: 0 0 -40px 0;
  text-align: center;
  /* header & footer height */
}

footer {
  width: 100%;
  height: 40px;
  position: fixed;
  overflow: hidden;
  bottom: 0;
  text-align: center;
  margin: auto;
}

/* text-title style */
.text-title {
  padding-top: 300px;
  font-size: 4rem;
}

.text-subtitle {
  font-size: 1.5rem;
  font-weight: 300;
}

.text-bold {
  color: rgb(23, 101, 80);
  font-weight: 400;
}

.text-content {
  font-size: 0.9rem;
  margin: 10px;
}
/* Home */
.home__container {
  width: 100%;
  min-height: 100%;
  position: relative;
  padding-top: 50px;
  margin: -50px 0 -40px 0;
  top: 0;
  left: 0;
  z-index: -10;
  /* footer height */
  background-image: url(/static/media/main.06f704a2.jpg);
  background-size: cover;
  background-attachment: fixed;
  text-align: center;
}

/* About page */
.about_title {
  margin-top: 100px;
}
.profile {
  width: 250px;
  border-radius: 50%;
}

.icon {
  margin-right: 10px;
}

/* Projects page */
.project__list {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.project__img {
  width: 700px;
  height: auto;
}

